/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
  padding: 1rem;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
  color: black;
}

.contenedor-app {
  display: block;
}

.app {
  margin-top: 2rem;
}

.titulos {
  font-size: 5rem;
  padding: 5rem 0;
  font-weight: 300;
}

.nombre-pagina {
  color: white;
}

.descripcion-pagina {
  color: white;
}

.navegacion {
  width: 95%;
  max-width: 200rem;
  margin: 0 auto;
  padding: 2rem;
  margin-top: 1rem;
  align-items: center;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 1rem;
}
.navegacion .campo {
  display: flex;
  flex-direction: column; /* Pone el label arriba del input/select */
  flex: 1; /* Permite que cada grupo ocupe un espacio proporcional */
  min-width: 200px; /* Ancho mínimo para cada conjunto */
}
.navegacion label {
  margin-bottom: 0.5rem; /* Espaciado entre el label y el input/select */
  font-weight: 700;
}
.navegacion input, .navegacion select {
  padding: 0.5rem;
  font-size: 1.5rem;
  width: 100%; /* Asegura que los inputs/selecciones ocupen todo el ancho del grupo */
}
.navegacion legend {
  font-size: 2rem;
}

.header {
  color: #FFFFFF;
  position: relative;
  width: 100%;
  z-index: 1;
}

/* Imagen de fondo independiente cuando no hay sesión */
.imagen-fondo {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../img/imagen.jpg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1;
}

/* Ajuste del encabezado cuando no hay sesión */
.centrado {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  text-align: center;
  padding-top: 5rem;
  position: relative;
}
.centrado h1 {
  margin: 0 auto;
  font-size: 5rem;
}

/* Ajuste del encabezado cuando hay sesión */
.encabezado-sesion {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 1rem 2rem;
  height: 16rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../img/imagen.jpg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

/* Contenedor del encabezado */
.encabezado {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 1rem 2rem;
  gap: 1rem;
}
.encabezado .menu-barras {
  position: relative;
}
.encabezado .menu-barras .menu-icon {
  background: none;
  border: none;
  font-size: 4rem;
  cursor: pointer;
  color: white;
  padding: 0.5rem;
}
.encabezado .menu-barras .menu-icon:hover {
  color: rgba(255, 255, 255, 0.8);
}
.encabezado .menu-barras .menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: none; /* Oculto por defecto */
  z-index: 999; /* Detrás del menú deslizante pero delante de la página */
}
.encabezado .menu-barras .menu-overlay.activo {
  display: block; /* Mostrar cuando se abre el menú */
}
.encabezado .menu-barras .menu-deslizante {
  position: fixed;
  top: 0;
  left: -100%; /* Oculto inicialmente */
  width: 250px;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
  color: white;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  gap: 1rem;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5);
  transition: left 0.3s ease;
  z-index: 1000; /* Asegura que el menú esté encima del overlay */
}
.encabezado .menu-barras .menu-deslizante a {
  text-decoration: none;
  color: white;
  font-size: 1.6rem;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}
.encabezado .menu-barras .menu-deslizante a:hover {
  background-color: rgba(255, 255, 255, 0.2);
}
.encabezado .menu-barras .menu-deslizante a.activo {
  background-color: rgba(255, 255, 255, 0.4);
  font-weight: bold;
}
.encabezado .menu-barras .menu-deslizante .menu-cerrar {
  align-self: flex-end;
  background: none;
  border: none;
  font-size: 2rem;
  color: white;
  cursor: pointer;
}
.encabezado .menu-barras .menu-deslizante .menu-cerrar:hover {
  color: rgba(255, 255, 255, 0.8);
}
.encabezado .menu-barras .menu-abierto {
  left: 0; /* Mostrar el menú */
}
.encabezado .logo {
  width: 10rem;
  height: auto;
}
.encabezado .titulos {
  font-size: 5rem;
  padding: 0;
  font-weight: 300;
  white-space: nowrap;
}
.encabezado .session {
  margin-left: auto;
}
.encabezado .session a {
  text-decoration: none;
  color: white;
  font-size: 2rem;
}

.contenedor {
  display: flex;
  width: 95%;
  border: 1px solid black;
  max-width: 200rem;
  margin-top: 1rem;
  overflow: hidden;
}
.contenedor p {
  color: black;
}
.contenedor .estudiantes {
  flex: 1; /* Ocupa el espacio disponible */
  overflow-y: auto; /* Hace que los estudiantes tengan scroll */
  max-height: 40vh; /* No permite que crezcan más allá de la pantalla */
}
.contenedor .fichas {
  flex: 1;
  max-height: 100vh;
  overflow-y: auto;
  padding-right: 10px;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}
.contenedor .info-estudiante {
  width: 75%;
  max-width: 160rem;
  padding: 20px;
  background-color: #f9f9f9;
  border-left: 2px solid #e1e1e1;
  position: sticky;
  top: 0;
  height: 100vh;
  overflow-y: auto; /* Permite hacer scroll en la información */
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.contenedor .info-estudiante.oculta {
  opacity: 0;
  visibility: hidden;
  transform: translateX(100%);
  pointer-events: none;
}

.oculto {
  display: none;
}

#selectGrupo, #selectAsignatura {
  min-width: 250px; /* Tamaño mínimo uniforme */
  max-width: 100%;
  white-space: nowrap; /* Evita que el texto se desborde */
  overflow: hidden;
  text-overflow: ellipsis; /* Agrega "..." si el texto es muy largo */
}

.campo select {
  width: 100%; /* Ambos selects ocuparán el ancho completo del contenedor */
  max-width: 100%; /* Evita que se desborde */
  padding: 8px; /* Espaciado interno para mejor apariencia */
  font-size: 16px; /* Tamaño de fuente uniforme */
  border: 1px solid #ccc; /* Bordes sutiles */
  border-radius: 5px; /* Bordes redondeados */
  background-color: #fff; /* Fondo blanco */
}

/* Permite que las opciones dentro del select puedan ajustarse */
#selectGrupo option, #selectAsignatura option {
  white-space: normal; /* Permite saltos de línea */
  word-wrap: break-word;
}

.ficha {
  background: white;
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  min-width: 150px;
  max-width: 40rem;
  width: 55rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 400;
  cursor: pointer;
  transition: transform 0.3s, background-color 0.3s;
  border: 1px solid black;
  margin: 1rem;
  position: relative; /* Asegura que el hover y la selección cubran toda la ficha */
  overflow: hidden; /* Evita que elementos internos sobresalgan */
}
.ficha .info-ficha {
  flex-grow: 1; /* Hace que la info-ficha use todo el espacio disponible */
  padding: 1rem;
  transition: color 0.3s ease-in-out;
}
.ficha:hover {
  background-color: rgb(13.61328125, 22.421875, 62.4609375);
  color: white;
}
.ficha:hover .info-ficha p {
  color: white;
}
.ficha:hover .estado-estudiante {
  color: white;
}

.ficha.seleccionado {
  background-color: rgb(13.61328125, 22.421875, 62.4609375);
  color: white;
  font-weight: bold;
  border: 2px solid rgb(20.7131578947, 34.1157894737, 95.0368421053);
}
.ficha.seleccionado .estado-estudiante {
  color: white;
}

.no-margin {
  margin: 0;
  padding: 0;
  font-weight: 300;
  font-size: 2rem;
}

.acciones input {
  width: 20rem;
  margin: 0.5rem;
}
.acciones button {
  width: 20rem;
  margin: 0.5rem;
}
.acciones .registro a {
  font-size: 2rem;
}

.info-detallada {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  border: 3px solid #e1e1e1;
  padding: 2rem;
}

/* Contenedor del login */
.contenedor_login {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  position: relative;
  z-index: 10;
}

/* Tarjeta del login */
.login_card {
  background: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  width: 100%;
  max-width: 400px;
  text-align: center;
}

/* Título */
.titulo {
  font-size: 24px;
  font-weight: bold;
  color: #222;
  margin-bottom: 10px;
}

/* Subtítulo */
.subtitulo {
  font-size: 14px;
  color: #666;
  margin-bottom: 20px;
}

/* Campos del formulario */
.campo {
  margin-bottom: 15px;
}

.form-control {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

/* Enlace Olvidó contraseña */
.enlace {
  display: block;
  text-align: left;
  font-size: 14px;
  color: #00A6E1;
  text-decoration: none;
  margin-bottom: 15px;
}

.enlace:hover {
  text-decoration: underline;
}

/* Botón de ingreso */
.boton {
  width: 100%;
  padding: 12px;
  background: #00A6E1;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s;
}

.boton:hover {
  background: #2563eb;
}

/* Registro */
.registro {
  margin-top: 15px;
  font-size: 14px;
  color: #666;
}

.registro a {
  color: #00A6E1;
  text-decoration: none;
  font-weight: bold;
}

.registro a:hover {
  text-decoration: underline;
}

.container {
  display: flex;
  flex-wrap: nowrap;
  margin: 20px;
}

/* Estilo para la lista de carros en mosaico */
#carros-container {
  display: flex;
  flex-direction: column;
  width: 400px;
  margin-right: 20px;
  background-color: white;
}
#carros-container label {
  font-size: 3rem;
  font-weight: 300;
  text-align: center;
  margin-bottom: 1rem;
  text-transform: uppercase;
}
#carros-container select {
  margin-bottom: 1rem;
  height: 4rem;
}

#carroSelectMedellin, #carroSelectRetiro {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  max-height: 390px; /* Ajustar el tamaño según sea necesario */
  position: relative; /* Para que los títulos sean relativos a este contenedor */
}

.carro-item {
  padding: 10px;
  margin: 5px;
  background-color: #f0f0f0;
  border: 1px solid #ddd;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.carro-item.selected {
  background-color: #accbe1;
  color: #1a1b15;
  border-color: rgba(255, 255, 255, 0.673);
  border-width: 3px;
  border-style: solid;
}

.carro-item:hover {
  background-color: rgb(180.3, 208.2, 228);
  color: black;
  border-color: #ffffff;
}

.carro-title {
  position: sticky;
  top: 0;
  border-bottom: 2px solid #ddd;
  padding: 10px;
  margin: 0;
  font-size: 1.2em;
  font-weight: bold;
  text-align: center;
  border: 1px solid black;
  border-radius: 10px;
  background-color: #0da6f3;
  color: white;
}

.fc .fc-button-group > .fc-button.fc-button-active, .fc .fc-button-group > .fc-button:active, .fc .fc-button-group > .fc-button:focus, .fc .fc-button-group > .fc-button:hover {
  z-index: 0;
}

#calendar-container {
  flex-grow: 1;
  margin-left: 20px;
  position: relative;
  margin-top: 30px;
}

#calendar {
  border: 1px solid #ddd;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border-radius: 8px;
  background-color: #fff;
  height: 65rem;
}

.fc .fc-day-today {
  background-color: rgba(0, 123, 255, 0.2) !important;
  color: #000;
}

.fc-event-main {
  background-color: #ef891d;
  border: 2px solid black;
  font-size: 1.8rem;
}

.reservar-button {
  position: absolute;
  top: -20px;
  right: 0;
  display: inline-block;
  padding: 10px 20px;
  color: #fff;
  background-color: #111c4e;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.reservar-button:hover {
  background-color: #0056b3;
}

/* -------------------- CONTAINER --------------------------*/
.modal {
  display: none;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
  padding-top: 60px;
}

.modal-content {
  background-color: #fefefe;
  margin: 5% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 30%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}
.modal-content p {
  color: black;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.modal-content form {
  display: flex;
  flex-direction: column;
}

.modal-content label {
  margin-top: 10px;
}

.modal-content input, .modal-content select, .modal-content button {
  margin-top: 5px;
  padding: 8px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.modal-content button {
  background-color: #0056b3;
  color: #fff;
  border: none;
  box-shadow: 0 4px #004494;
}

.modal-content button:hover {
  background-color: #003d82;
  box-shadow: 0 4px #002b61;
}

.modal-content button:active {
  box-shadow: 0 2px #002b61;
  transform: translateY(2px);
}

.btn-3d {
  display: inline-block;
  padding: 10px 20px;
  margin-top: 10px;
  color: #fff;
  background-color: #ffa81d;
  border: none;
  border-radius: 5px;
  box-shadow: 0 4px #004494;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-3d:hover {
  background-color: #003d82;
  box-shadow: 0 4px #002b61;
}

.btn-3d:active {
  box-shadow: 0 2px #002b61;
  transform: translateY(2px);
}

.btn-3d:disabled {
  background-color: #ccc;
  cursor: not-allowed;
  box-shadow: none;
}

/* -------------------- MODAL --------------------------*/
.toast {
  visibility: hidden;
  min-width: 250px;
  margin-left: -125px;
  background-color: #333;
  color: #fff;
  text-align: center;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  left: 50%;
  bottom: 30px;
  font-size: 17px;
}

.toast.show {
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

@-webkit-keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}
@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}
@-webkit-keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}
@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}
